





























import { Component, Prop, Vue } from 'vue-property-decorator'
import { mixins } from 'vue-class-component'
import ControlMixins from '../ControlMixins'
import { FieldInputDefinition, FieldInputOptions } from '@/components/type'

@Component
export default class InputAttr extends mixins(ControlMixins) {
  get def (): FieldInputDefinition {
    return this.activeControl as FieldInputDefinition
  }

  get options (): FieldInputOptions {
    return this.def.options
  }
}
