



































import { Component, Vue } from 'vue-property-decorator'

@Component
export default class App extends Vue {
  action = 'http://localhost:3000/form/uploadFile'

  formJson = ''

  formData = ''

  data: any = null

  value: any = null

  edit = true

  run () {
    if (!this.formJson) {
      this.data = null
    } else {
      this.data = JSON.parse(this.formJson)
    }

    if (!this.formData) {
      this.value = null
    } else {
      this.value = JSON.parse(this.formData)
    }
  }
}
